<template>
  <div class="app-video">
    <img v-show="!isHidePlayAndPause"
         @click.once="init()"
         :class="playAndPauseInVideoDomCss"
         id="playAndPauseInVideo"
         src="../../static/images/play-big.png" alt="播放"
         class="playAndPauseInVideo">
    <img v-show="!isShowVideo" :src="video_data.img_url" alt="封面" class="videoImg">
    <video id="video" preload @click="isinit&&stateChange()">
      <source v-if="isinit" :src="video_data.video_url" type="video/mp4">
    </video>
    <div class="controls">
      <img id="playAndPauseInControls" src="../../static/images/play.png" alt="播放" class="playAndPauseInControls"
           @click="stateChange()">
      <div class="time"><span id="nowtime">{{nowTime}}</span>&ensp;/&ensp;<span id="alltime">{{allTime}}</span></div>
      <img id="fullscreen" src="../../static/images/fullscreen.png" alt="全屏" class="fullscreen"
           @click="!isFullScreen?launchFullScreen():exitFullscreen()">
      <div class="volumeWindow">
        <img id="volume" src="../../static/images/volume1.png" alt="音量" class="volume">
        <div id="volumeBar" class="volumeBar">
          <div id="volumeRoad" class="volumeRoad">
            <div id="volumeLine" class="volumeLine">
              <div id="volumeDot" class="volumeDot"></div>
            </div>
          </div>
        </div>
      </div>
      <div class="progressBar">
        <div id="progressRoad" class="progressRoad">
          <div id="progressLine" class="progressLine">
            <div id="progressDot" class="progressDot"></div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'app-video',
    props: ['video_data'],
    data: function data() {
      return {
        isinit: false, //是否初始化
        videoWindowDom: null, //视频窗口dom对象
        videoDom: null, //video的dom对象
        isShowVideo: false, //是否显示视频
        isHidePlayAndPause: false, //显示隐藏播放或暂停按钮（视频中间）
        playAndPauseInVideoDom: null, //播放或暂停图标的dom对象(视频中间)
        playAndPauseInControlsDom: null, //播放或暂停图标dom对象(操作面板)
        progressRoadDom: null, //滑动轨道dom(进度条)
        progressLineDom: null, //视频进度条dom对象(进度条)
        progressDotDom: null, //滑动的小圆点dom(进度条)
        VolumeDOM: null, //音量图标dom
        volumeRoadDom: null, ////滑动轨道dom(音量)
        volumeLineDom: null, //音量刻度条dom对象(音量)
        volumeDotDom: null, //滑动的小圆点dom(音量)
        FullScreenDom: null, //全屏按钮dom对象
        nowTime: '00:00', //播放时间
        allTime: '00:00', //视频总长
        playAndPauseInVideoDomCss: '', //播放或暂停按钮动画
        isFullScreen: false, //视屏是否全屏
        VolumeObj: 0.5 //音量
      };
    },
    watch: {},
    methods: {
      init: function () {
        let self = this;

        //获取DOM对象
        self.videoDom = document.getElementById('video');
        self.playAndPauseInVideoDom = document.getElementById('playAndPauseInVideo');
        self.playAndPauseInControlsDom = document.getElementById('playAndPauseInControls');
        self.progressRoadDom = document.getElementById('progressRoad');
        self.progressLineDom = document.getElementById('progressLine');
        self.progressDotDom = document.getElementById('progressDot');
        if (self.VolumeDOM === null) self.VolumeDOM = document.getElementById('volume');
        if (self.volumeRoadDom === null) self.volumeRoadDom = document.getElementById('volumeRoad');
        if (self.volumeLineDom === null) self.volumeLineDom = document.getElementById('volumeLine');

        //注册视频事件
        self.videoDom.onloadstart = function (ev) {
          //客户端开始请求数据
          console.log('客户端开始请求数据');
        };
        self.videoDom.onprogress = function (ev) {
          //客户端正在请求数据
          console.log('客户端正在请求数据');
        };
        self.videoDom.onloadedmetadata = function (ev) {
          //成功获取资源长度
          console.log('成功获取资源长度');
          console.log('资源长度' + self.videoDom.duration);
          self.allTime = self.calculation(self.videoDom.duration).rs;
        };
        self.videoDom.ontimeupdate = function (ev) {
          //视频的播放位置改变
          self.nowTime = self.calculation(self.videoDom.currentTime).rs;
          self.progressLineDom.style.width = self.videoDom.currentTime / self.videoDom.duration * self.progressRoadDom.offsetWidth + "px";
        };
        self.videoDom.onvolumechange = function (ev) {
          //音量改变
          console.log('音量改变' + self.videoDom.volume);
          self.volumeLineDom.style.height = self.videoDom.volume * self.volumeRoadDom.offsetHeight + 'px';
          if (self.videoDom.volume === 0) {
            self.VolumeDOM.src = self.app_config.staticPath + '/images/volume0.png';
          } else {
            self.VolumeDOM.src = self.app_config.staticPath + '/images/volume1.png';
          }
        };
        self.videoDom.onwaiting = function (ev) {
          //等待数据，并非错误
          console.log('等待数据');
        };
        self.videoDom.onended = function (ev) {
          //播放结束
          console.log('播放结束');
        };
        self.isShowVideo = true; //显示视频，隐藏封面(开始缓冲)
        self.isHidePlayAndPause = true; //隐藏视频中间播放按钮
        self.playAndPauseInVideoDom.src = self.app_config.staticPath + '/images/play-big.png'; //
        self.playAndPauseInControlsDom.src = self.app_config.staticPath + '/images/pause.png'; //
        self.videoDom.volume = self.VolumeObj;
        console.log('当前音量' + self.videoDom.volume);
        self.videoDom.play(); //视频播放
        self.isinit = true; //初始化完成
        self.setProgressMouseFn(); //注册进度条相关事件
      },
      stateChange: function () {
        //改变播放状态
        let self = this;
        if (self.videoDom != null) {
          self.isHidePlayAndPause = false;
          self.playAndPauseInVideoDomCss = 'tobig';
          window.setTimeout(function () {
            self.isHidePlayAndPause = true;
            self.playAndPauseInVideoDomCss = '';
          }, 500);
          if (self.videoDom.paused) {
            self.playAndPauseInVideoDom.src = self.app_config.staticPath + '/images/play-big.png';
            self.playAndPauseInControlsDom.src = self.app_config.staticPath + '/images/pause.png';
            self.videoDom.play();
          } else {
            self.playAndPauseInVideoDom.src = self.app_config.staticPath + '/images/pause-big.png';
            self.playAndPauseInControlsDom.src = self.app_config.staticPath + '/images/play.png';
            self.videoDom.pause();
          }
        } else {
          self.init();
        }
      },
      calculation: function (value) {
        //格式化时间串
        let num = 0,
          m = 0,
          s = 0,
          rs = '';
        let v_int = parseInt(value); //int
        if (v_int === value) {
          num = v_int;
        } else {
          num = v_int + 1;
        }

        s = num % 60;
        m = parseInt(num / 60);

        if (m > 9) {
          rs += m + ':';
        } else {
          rs += '0' + m + ':';
        }

        if (s > 9) {
          rs += s;
        } else {
          rs += '0' + s;
        }

        return {
          num: num,
          m: m,
          s: s,
          rs: rs
        };
      },
      getPoint: function (obj) {
        //获取某元素以浏览器左上角为原点的坐标
        let t = obj.offsetTop; //获取该元素对应父容器的上边距
        let l = obj.offsetLeft; //对应父容器的上边距
        //判断是否有父容器，如果存在则累加其边距
        while (obj = obj.offsetParent) {
          //等效 obj = obj.offsetParent;while (obj != undefined)
          t += obj.offsetTop; //叠加父容器的上边距
          l += obj.offsetLeft; //叠加父容器的左边距
        }
        return {
          top: t,
          left: l
        };
      },
      setProgressMouseFn: function () {
        //注册进度条事件
        let self = this,
          isDrag = false,
          mouseX = 0,
          oldWidth = 0,
          fullwidth = self.progressRoadDom.offsetWidth,
          slide = false;
        self.progressDotDom.addEventListener('mousedown', function (ev) {
          isDrag = true;
          mouseX = ev.clientX;
          oldWidth = self.progressLineDom.offsetWidth;
          return false;
        });
        document.addEventListener('mousemove', function (ev) {
          if (isDrag) {
            slide = true;
            let l = ev.clientX - mouseX + oldWidth;
            if (l < 0) {
              l = 0;
            } else if (l > fullwidth) {
              l = fullwidth;
            }
            self.videoDom.currentTime = l * self.videoDom.duration / fullwidth;
          }
        });
        document.addEventListener('mouseup', function (ev) {
          if (isDrag) {
            isDrag = false;
          }
          return false;
        });
        self.progressRoadDom.addEventListener('click', function (ev) {
          if (!isDrag && !slide) {
            self.videoDom.currentTime = (ev.clientX + document.documentElement.scrollLeft - self.getPoint(self.progressRoadDom).left) * self.videoDom.duration / fullwidth;
          }
          slide = false;
        });
      },
      launchFullScreen: function () {
        //视频全屏
        let self = this;
        if (self.videoWindowDom === null) self.videoWindowDom = document.getElementsByClassName('app-video')[0];
        if (self.FullScreenDom === null) self.FullScreenDom = document.getElementById('fullscreen');
        self.FullScreenDom.src = self.app_config.staticPath + '/images/originalscreen.png';
        self.isFullScreen = true;
        if (self.videoWindowDom.requestFullscreen) {
          return self.videoWindowDom.requestFullScreen();
        } else if (self.videoWindowDom.webkitRequestFullScreen) {
          return self.videoWindowDom.webkitRequestFullScreen();
        } else if (self.videoWindowDom.mozRequestFullScreen) {
          return self.videoWindowDom.mozRequestFullScreen();
        } else {
          return self.videoWindowDom.msRequestFullscreen();
        }
      },
      exitFullscreen: function () {
        //退出视频全屏
        let self = this;
        if (self.videoWindowDom === null) self.videoWindowDom = document.getElementsByClassName('app-video')[0];
        if (self.FullScreenDom === null) self.FullScreenDom = document.getElementById('fullscreen');
        self.FullScreenDom.src = self.app_config.staticPath + '/images/fullscreen.png';
        self.isFullScreen = false;
        if (document.exitFullscreen) {
          return document.exitFullscreen();
        } else if (document.webkitExitFullscreen) {
          return document.webkitExitFullscreen();
        } else if (document.mozExitFullScreen) {
          return document.mozExitFullScreen();
        } else {
          return document.msExitFullScreen();
        }
      },
      initVolume: function () {
        let self = this;

        if (self.VolumeDOM === null) self.VolumeDOM = document.getElementById('volume');
        if (self.volumeRoadDom === null) self.volumeRoadDom = document.getElementById('volumeRoad');
        if (self.volumeLineDom === null) self.volumeLineDom = document.getElementById('volumeLine');
        self.volumeDotDom = document.getElementById('volumeDot');

        let isDrag = false,
          mouseY = 0,
          oldHeight = 0,
          fullheight = self.volumeRoadDom.offsetHeight,
          slide = false,
          temp = 0.5;

        self.volumeDotDom.addEventListener('mousedown', function (ev) {
          isDrag = true;
          mouseY = ev.clientY;
          oldHeight = self.volumeLineDom.offsetHeight;
          return false;
        });
        document.addEventListener('mousemove', function (ev) {
          if (isDrag) {
            slide = true;
            let l = mouseY - ev.clientY + oldHeight;
            if (l < 0) {
              l = 0;
            } else if (l > fullheight) {
              l = fullheight;
            }
            if (self.videoDom != null) {
              self.videoDom.volume = l / fullheight;
              self.VolumeObj = self.videoDom.volume;
            } else {
              temp = self.VolumeObj = l / fullheight;
              if (self.VolumeObj === 0) {
                self.VolumeDOM.src = self.app_config.staticPath + '/images/volume0.png';
              } else {
                self.VolumeDOM.src = self.app_config.staticPath + '/images/volume1.png';
              }
              self.volumeLineDom.style.height = self.VolumeObj * self.volumeRoadDom.offsetHeight + 'px';
            }
          }
        });
        document.addEventListener('mouseup', function (ev) {
          if (isDrag) {
            isDrag = false;
          }
          return false;
        });

        self.volumeRoadDom.addEventListener('click', function (ev) {
          if (!isDrag && !slide) {
            if (self.videoDom != null) {
              self.videoDom.volume = (fullheight - (ev.clientY + document.documentElement.scrollTop - self.getPoint(self.volumeRoadDom).top)) / fullheight;
            } else {
              temp = self.VolumeObj = self.videoDom.volume;
              if (self.VolumeObj === 0) {
                self.VolumeDOM.src = self.app_config.staticPath + '/images/volume0.png';
              } else {
                self.VolumeDOM.src = self.app_config.staticPath + '/images/volume1.png';
              }
              self.volumeLineDom.style.height = self.VolumeObj * self.volumeRoadDom.offsetHeight + 'px';
            }
          }
          slide = false;
        });

        self.VolumeDOM.onclick = function () {
          if (self.videoDom != null) {
            if (self.videoDom.volume === 0) self.VolumeObj === 0 ? self.videoDom.volume = self.VolumeObj = 0.5 : self.videoDom.volume = self.VolumeObj; else self.videoDom.volume = 0;
          } else {
            if (self.VolumeObj === 0) self.VolumeObj = temp; else self.VolumeObj = 0;
            if (self.VolumeObj === 0) {
              self.VolumeDOM.src = self.app_config.staticPath + '/images/volume0.png';
            } else {
              self.VolumeDOM.src = self.app_config.staticPath + '/images/volume1.png';
            }
            self.volumeLineDom.style.height = self.VolumeObj * self.volumeRoadDom.offsetHeight + 'px';
          }
        };
      }
    },
    mounted() {
      this.initVolume();
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

  .app-video {
    position: relative;
    width: 100%;
    height: 100%;
    background-color: #ddd
  }

  .app-video > .playAndPauseInVideo {
    display: block;
    cursor: pointer;
    position: absolute;
    width: 90px;
    height: 90px;
    border-radius: 50%;
    background-color: rgba(0, 0, 0, .3);
    top: 50%;
    left: 50%;
    margin: -45px 0 0 -45px;
    z-index: 1
  }

  .app-video > .tobig {
    -webkit-animation: big .5s linear forwards;
    animation: big .5s linear forwards
  }

  .app-video > .videoImg {
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%
  }

  .app-video > video {
    display: block;
    width: 100%;
    height: 100%
  }

  .app-video > .controls {
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 30px;
    line-height: 30px;
    background-color: rgba(0, 0, 0, .3);
    color: #fff
  }

  .app-video > .controls > .fullscreen, .app-video > .controls > .playAndPauseInControls, .app-video > .controls > .volumeWindow {
    float: left;
    width: 30px;
    height: 30px;
    display: block;
    box-sizing: border-box;
    padding: 5px;
    cursor: pointer
  }

  .app-video > .controls > .volumeWindow {
    padding: 0
  }

  .app-video > .controls > .volumeWindow:hover > .volumeBar {
    visibility: visible
  }

  .app-video > .controls > .volumeWindow > img.volume {
    display: block;
    box-sizing: border-box;
    width: 30px;
    height: 30px;
    padding: 5px
  }

  .app-video > .controls > .time {
    float: left;
    font-size: 12px;
    width: 90px;
    text-align: center
  }

  .app-video > .controls > .progressBar {
    position: relative;
    margin-left: 126px;
    margin-right: 76px;
    height: 30px
  }

  .app-video > .controls > .progressBar > .progressRoad {
    position: absolute;
    top: 14px;
    left: 0;
    width: 100%;
    height: 2px;
    background-color: hsla(0, 0%, 100%, .3);
    cursor: pointer;
    box-sizing: border-box
  }

  .app-video > .controls > .progressBar > .progressRoad > .progressLine {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 0;
    background-color: #4285f4
  }

  .app-video > .controls > .progressBar > .progressRoad > .progressLine > .progressDot {
    position: absolute;
    top: -5px;
    right: -6px;
    background-color: #4285f4;
    border-radius: 50%;
    width: 12px;
    height: 12px;
    cursor: pointer
  }

  .app-video > .controls > .fullscreen, .app-video > .controls > .volumeWindow {
    float: right;
    margin-left: 5px
  }

  .app-video > .controls > .volumeWindow > .volumeBar {
    visibility: hidden;
    position: absolute;
    top: -100px;
    right: 35px;
    width: 30px;
    height: 100px;
    background-color: rgba(0, 0, 0, .3)
  }

  .app-video > .controls > .volumeWindow > .volumeBar > .volumeRoad {
    position: absolute;
    top: 12px;
    bottom: 8px;
    left: 14px;
    width: 2px;
    background-color: hsla(0, 0%, 100%, .3);
    cursor: pointer
  }

  .app-video > .controls > .volumeWindow > .volumeBar > .volumeRoad > .volumeLine {
    position: absolute;
    left: 0;
    bottom: 0;
    width: 2px;
    height: 40px;
    background-color: #4285f4
  }

  .app-video > .controls > .volumeWindow > .volumeBar > .volumeRoad > .volumeLine > .volumeDot {
    position: absolute;
    top: -6px;
    left: -5px;
    background-color: #4285f4;
    border-radius: 50%;
    width: 12px;
    height: 12px;
    cursor: pointer
  }

  @-webkit-keyframes big {
    0% {
      width: 50px;
      height: 50px;
      margin: -25px 0 0 -25px;
      opacity: 1
    }
    to {
      width: 90px;
      height: 90px;
      margin: -45px 0 0 -45px;
      opacity: 0
    }
  }

  @keyframes big {
    0% {
      width: 50px;
      height: 50px;
      margin: -25px 0 0 -25px;
      opacity: 1
    }
    to {
      width: 90px;
      height: 90px;
      margin: -45px 0 0 -45px;
      opacity: 0
    }
  }
</style>



