<template>
  <div class="app-product">
    <app-breadcrumb :breadcrumb_data="breadcrumb_data"></app-breadcrumb>
    <div class="middle-content">
      <div class="left">
        <div class="video-content">
          <app-video :video_data="video_data"></app-video>
        </div>
        <p class="title">{{cate_data.title}}</p>
        <p class="meta-introduction">{{cate_data.introduction}}</p>
        <p class="meta-description">{{cate_data.description}}</p>
        <p class="necessary-time">烹饪时间：{{cate_data.maketime}}</p>
        <p class="expense">预计费用：{{cate_data.expense}}</p>
        <div @click="change_show_popup(true)" class="btn favorite">
          <img src="../../static/images/collection.png" alt="收藏">
          <span>收藏</span>
        </div>
        <div class="line"></div>
        <div class="materials">
          <p class="title">材料 <span>（{{cate_data.people}}）</span></p>
          <ul>
            <li v-for="listitem in cate_data.materials">
              <span>{{listitem.name}}</span>
              <span>{{listitem.yongliang}}</span>
            </li>
          </ul>
        </div>
        <div class="step">
          <p class="title">步骤</p>
          <ul>
            <li v-for="listitem in cate_data.step">
              <span>{{listitem.buzhouid}}</span>
              <span>{{listitem.buzhoucontent}}</span>
            </li>
          </ul>
        </div>
        <div class="prompt_information">
          <p class="title">提示说明</p>
          <div>{{prompt_information}}</div>
        </div>
        <div class="submission">
          <p class="title">投稿</p>
          <ul>
            <li v-for="listitem in submission_data.list">
              <img :src="listitem.showimg" alt="投稿图片">
              <div class="user-info">
                <img :src="listitem.touxiang" alt="头像">
                <span class="name">{{listitem.nickname}}</span>
                <span class="time">{{listitem.create_date}}</span>
              </div>
              <div class="content">{{listitem.content}}</div>
            </li>
          </ul>
          <a @click="change_show_popup(true)"
             v-if="submission_data.allcount> submission_data.list.length">查看更多（共{{submission_data.allcount}}个）</a>
          <div class="btn favorite middle">
            <img src="../../static/images/camera.png" alt="投稿">
            <span>成品投稿</span>
          </div>
        </div>
        <div class="comment">
          <p class="title">评论</p>
          <ul>
            <li v-for="listitem in comment_data.list">
              <img :src="listitem.touxiang" alt="头像">
              <div>
                <span class="name">{{listitem.nickname}}</span>
                <div class="content">{{listitem.content}}</div>
                <span class="time">{{listitem.create_date}}</span>
              </div>
            </li>
          </ul>
          <div @click="change_show_popup(true)" class="btn favorite middle">
            <img src="../../static/images/message.png" alt="评论">
            <span>发表评论</span>
          </div>
        </div>
      </div>
      <div class="right">
        <app-special :special_data="special_data"></app-special>
      </div>
      <app-popup v-show="show_popup" :show_popup="show_popup" @c_show_popup="change_show_popup"></app-popup>

    </div>
  </div>
</template>

<script>
  import app_breadcrumb from '@/components/App-breadcrumb'
  import app_video from '@/components/App-video'
  import app_special from '@/components/App-special'
  import app_popup from '@/components/App-popup'

  let video_defautData = {
      img_url: '',
      video_url: ''
    },
    special_defautData = [],
    cate_defautData = {
      title: '',
      introduction: '',
      description: '',
      maketime: '',
      expense: ''
    },
    submission_defautData = {
      allcount: '',
      list: []
    },
    comment_defautData = {
      allcount: '',
      list: []
    };


  export default {
    name: 'app-product',
    data() {
      return {
        breadcrumb_data: [],
        special_data: special_defautData,
        video_data: video_defautData,
        cate_data: cate_defautData,
        submission_data: submission_defautData,
        comment_data: comment_defautData,
        prompt_information: '无',
        show_popup: false
      }
    },
    watch: {
      videolist_data: function () {
        this.temp_data = this.videolist_data;
      }
    },
    components: {
      'app-video': app_video,
      'app-breadcrumb': app_breadcrumb,
      'app-special': app_special,
      'app-popup': app_popup
    },
    methods: {
      change_show_popup: function (data) {
        this.show_popup = data;
      }
    },
    mounted() {
      let self = this;

      self.$http.get(self.app_config.basePath + '/videoinfo', {
        params: {
          nowid: self.$route.query.id
        }
      }).then(function (response) {
        self.breadcrumb_data = response.data.breadcrumb_data;
        self.video_data = response.data.video_data;
        self.cate_data = response.data.cate_info;
        self.prompt_information = response.data.prompt_information;
      }).catch(function (err) {
        console.log(err);
      });

      self.$http.get(self.app_config.basePath + '/imgcommentinfo', {
        params: {
          nowid: self.$route.query.id
        }
      }).then(function (response) {
        self.submission_data = response.data;
      }).catch(function (err) {
        console.log(err);
      });

      self.$http.get(self.app_config.basePath + '/textcommentinfo', {
        params: {
          nowid: self.$route.query.id
        }
      }).then(function (response) {
        self.comment_data = response.data;
      }).catch(function (err) {
        console.log(err);
      });

      self.$http.get(self.app_config.basePath + '/specialinfo', {
        params: {}
      }).then(function (response) {
        let temp = [],
          titlelength = 25,
          descriptionlength = 40;
        response.data.forEach(function (item, index) {
          let obj = {
            id: item.id,
            title: item.title,
            description: item.description,
            img_url: item.img_url
          };
          if (obj.title.length >= titlelength) {
            obj.title = item.title.slice(0, titlelength) + '...';
          }
          if (obj.description.length >= descriptionlength) {
            obj.description = item.description.slice(0, descriptionlength) + '...';
          }
          temp.push(obj);
        });
        self.special_data = temp;
      }).catch(function (err) {
        console.log(err);
      });
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

  .app-product {
    width: 100%
  }

  .app-product > .middle-content {
    width: 1000px;
    margin: auto;
    padding: 40px 0;
    overflow: hidden
  }

  .app-product > .middle-content > .left {
    float: left;
    width: 689px;
    font-size: 16px;
    padding-right: 20px;
    box-sizing: border-box
  }

  .app-product > .middle-content > .right {
    float: right;
    width: 311px
  }

  .app-product > .middle-content > .left > .video-content {
    height: 560px;
    width: 560px
  }

  .app-product > .middle-content > .left > .title {
    font-size: 26px;
    font-weight: 700;
    margin: 40px 0 8px
  }

  .app-product > .middle-content > .left > .meta-introduction {
    padding-top: 8px;
    margin: 0 0 8px;
    font-size: 14px;
    color: #5e5e5e
  }

  .app-product > .middle-content > .left > .meta-description {
    padding-top: 8px;
    line-height: 1.5
  }

  .app-product > .middle-content > .left > .necessary-time {
    margin: 16px 0 0;
    line-height: 1.5
  }

  .app-product > .middle-content > .left > .expense {
    margin: 4px 0 16px;
    line-height: 1.5
  }

  .app-product > .middle-content > .left > .comment,
  .app-product > .middle-content > .left > .materials,
  .app-product > .middle-content > .left > .prompt_information,
  .app-product > .middle-content > .left > .step,
  .app-product > .middle-content > .left > .submission {
    margin-bottom: 40px
  }

  .app-product > .middle-content > .left > .comment > p.title,
  .app-product > .middle-content > .left > .materials > p.title,
  .app-product > .middle-content > .left > .prompt_information > p.title,
  .app-product > .middle-content > .left > .step > p.title,
  .app-product > .middle-content > .left > .submission > p.title {
    font-size: 20px;
    margin: 0;
    padding-bottom: 8px;
    border-bottom: 1px solid #cfcfcf;
    font-weight: 700
  }

  .app-product > .middle-content > .left > .materials > p.title > span,
  .app-product > .middle-content > .left > .step > p.title > span {
    font-weight: 400;
    font-size: 12px
  }

  .app-product > .middle-content > .left > .prompt_information > div {
    padding-top: 25px;
    font-size: 15px
  }

  .app-product > .middle-content > .left > .submission > ul {
    list-style: none;
    margin: 0 -20px 0 0;
    padding: 0;
    overflow: hidden
  }

  .app-product > .middle-content > .left > .comment > ul {
    list-style: none;
    margin: 0;
    padding: 0;
    overflow: hidden
  }

  .app-product > .middle-content > .left > .submission > ul > li {
    float: left;
    width: 33.33%;
    padding-right: 20px;
    margin-top: 25px;
    box-sizing: border-box
  }

  .app-product > .middle-content > .left > .comment > ul > li {
    overflow: hidden;
    border-bottom: 1px solid #cfcfcf
  }

  .app-product > .middle-content > .left > .submission > ul > li > img {
    width: 100%
  }

  .app-product > .middle-content > .left > .comment > ul > li > img {
    float: left;
    display: block;
    height: 50px;
    width: 50px;
    border-radius: 50%;
    margin: 25px 20px 25px 0
  }

  .app-product > .middle-content > .left > .comment > ul > li > div {
    float: left;
    width: 599px;
    padding: 25px 0
  }

  .app-product > .middle-content > .left > .comment > ul > li > div > span {
    font-size: 14px
  }

  .app-product > .middle-content > .left > .comment > ul > li > div > span.name {
    color: #262626;
    font-weight: 700
  }

  .app-product > .middle-content > .left > .comment > ul > li > div > div.content {
    font-size: 14px;
    line-height: 1.6;
    margin: 8px 0;
    width: 100%;
    word-wrap: break-word
  }

  .app-product > .middle-content > .left > .comment > ul > li > div > span.time {
    color: #5e5e5e
  }

  .app-product > .middle-content > .left > .submission > ul > li > div.user-info {
    overflow: hidden;
    margin: 10px 0
  }

  .app-product > .middle-content > .left > .submission > ul > li > div.user-info > img {
    float: left;
    display: block;
    height: 50px;
    width: 50px;
    border-radius: 50%
  }

  .app-product > .middle-content > .left > .submission > ul > li > div.user-info > span {
    display: block;
    height: 25px;
    line-height: 25px;
    padding-left: 60px;
    font-size: 14px;
    color: #5e5e5e
  }

  .app-product > .middle-content > .left > .submission > ul > li > div.user-info > span.name {
    font-weight: 700;
    color: #262626
  }

  .app-product > .middle-content > .left > .submission > ul > li > .content {
    width: 100%;
    word-wrap: break-word;
    margin-top: 10px;
    font-size: 14px;
    line-height: 1.6
  }

  .app-product > .middle-content > .left > .submission > a {
    text-align: center;
    display: block;
    margin-top: 40px;
    font-size: 18px;
    color: #c6b363;
    text-decoration: none;
    cursor: pointer
  }

  .app-product > .middle-content > .left > .materials > ul,
  .app-product > .middle-content > .left > .step > ul {
    margin: auto;
    padding: 10px 0 0;
    list-style: none;
    font-size: 15px
  }

  .app-product > .middle-content > .left > .materials > ul > li,
  .app-product > .middle-content > .left > .step > ul > li {
    height: 35px;
    line-height: 35px;
    display: block;
    width: 100%;
    overflow: hidden;
    border-bottom: 1px solid #f1f1f2
  }

  .app-product > .middle-content > .left > .materials > ul > li > span:first-child,
  .app-product > .middle-content > .left > .step > ul > li > span:first-child {
    float: left
  }

  .app-product > .middle-content > .left > .materials > ul > li > span:last-child,
  .app-product > .middle-content > .left > .step > ul > li > span:last-child {
    float: right
  }

  .app-product > .middle-content > .left > .line {
    height: 1px;
    background-color: #cfcfcf;
    margin: 40px 0
  }

  .app-product > .middle-content > .left .btn {
    margin-left: 2px;
    width: 300px;
    height: 40px;
    border-radius: 2px;
    border: 1px solid #bfae68;
    box-shadow: inset 0 0 3px #bfae68, 0 0 3px #bfae68;
    font-weight: 700;
    color: #bfae68;
    text-align: center;
    cursor: pointer
  }

  .app-product > .middle-content > .left .btn.middle {
    margin: 40px auto
  }

  .app-product > .middle-content > .left .btn > img {
    display: inline-block;
    width: 20px;
    height: 20px;
    margin: 10px 0;
    vertical-align: middle
  }

  .app-product > .middle-content > .left .btn > span {
    display: inline-block;
    margin-left: 5px;
    line-height: 40px;
    vertical-align: middle
  }

</style>



