<template>
  <div class="app-special">
    <p class="title">特辑</p>
    <ul class="special-list">
      <li v-for="listitem in special_data">
        <router-link :to="{
          name:'app-specialinfo',
            query:{
              id:listitem.id
            }
          }">
          <img :src="listitem.img_url">
          <div class="title">{{listitem.title}}</div>
          <div class="description">{{listitem.description}}</div>
        </router-link>
      </li>
    </ul>
  </div>
</template>

<script>
  export default {
    name: 'app-special',
    props: ['special_data'],
    data() {
      return {}
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

  .app-special {
    width: 100%;
    box-sizing: border-box;
    padding-left: 20px;
    border-left: 1px solid #cfcfcf
  }

  .app-special > .title {
    font-size: 20px;
    font-weight: 700;
    margin: auto;
    padding-bottom: 8px;
    border-bottom: 1px solid #c6b363
  }

  .app-special > .special-list {
    width: 100%;
    list-style: none;
    margin: auto;
    padding: 0;
    overflow: hidden
  }

  .app-special > .special-list > li {
    width: 100%;
    margin-top: 25px;
    float: left
  }

  .app-special > .special-list > li > a {
    width: 100%;
    display: block;
    text-decoration: none
  }

  .app-special > .special-list > li > a[data-v-4255b748]:hover {
    opacity: .8;
    filter: alpha(opacity=80)
  }

  .app-special > .special-list > li > a > img {
    display: block;
    width: 100%;
    height: 170px;
    background-image: url(data:image/png;base64,);
    background-size: 100%
  }

  .app-special > .special-list > li > a > .title {
    width: 100%;
    margin: 10px 0 6px;
    font-size: 16px;
    display: block;
    color: #262626;
    font-weight: 700;
    line-height: 1.5
  }

  .app-special > .special-list > li > a > .description {
    width: 100%;
    color: #5e5e5e;
    font-size: 12px;
    margin: 6px 0 10px;
    line-height: 1.5
  }
</style>



