<template>
  <div class="app-home">
    <div class="video-content">
      <div class="inner">
        <app-video :video_data="video_data"></app-video>
      </div>
    </div>
    <div class="middle-content">
      <div class="videolist">
        <app-classify :classify_data="classify_data"></app-classify>
        <app-videolist :videolist_data="videolist_data"></app-videolist>
      </div>
      <div class="special">
        <app-special :special_data="special_data"></app-special>
      </div>
    </div>
  </div>
</template>

<script>
  import app_video from '@/components/App-video'
  import app_classify from '@/components/App-classify'
  import app_special from '@/components/App-special'
  import app_videolist from '@/components/App-videolist'

  export default {
    name: 'app-home',
    data() {
      return {
        video_data: {
          img_url: '',
          video_url: ''
        },
        classify_data: [],
        videolist_data: {
          title: '最新视频',
          pagemsg: {
            nowpage: 0,
            sumpage: 0
          },
          list: []
        },
        special_data: []
      }
    },
    components: {
      'app-video': app_video,
      'app-classify': app_classify,
      'app-special': app_special,
      'app-videolist': app_videolist,
    },
    watch: {
      '$route': function (route) {
        this.videolist_func();
      }
    },
    methods: {
      videolist_func: function () {
        let self = this;
        self.$http.get(self.app_config.basePath + '/videolistinfo', {
          params: {
            page: this.$route.query.page ? this.$route.query.page : 1
          }
        }).then(function (response) {
          let temp = {
              title: response.data.title,
              pagemsg: {
                nowpage: parseInt(response.data.pagemsg.nowpage),
                sumpage: parseInt(response.data.pagemsg.sumpage)
              },
              list: []
            },
            titlelength = 20,
            descriptionlength = 30,
            ingredientslength = 15;
          response.data.list.forEach(function (item, index) {
            let obj = {
              id: item.id,
              img_url: item.img_url,
              title: item.title,
              description: item.description,
              ingredients: item.ingredients
            };
            if (obj.title.length >= titlelength) {
              obj.title = item.title.slice(0, titlelength) + '...';
            }
            if (obj.description.length >= descriptionlength) {
              obj.description = item.description.slice(0, descriptionlength) + '...';
            }
            if (obj.ingredients.length >= ingredientslength) {
              obj.ingredients = item.ingredients.slice(0, ingredientslength) + '...';
            }
            temp.list.push(obj);
          });
          self.videolist_data = temp;
        }).catch(function (err) {
          console.log(err);
        });
      }
    },
    mounted() {
      let self = this;

      self.$http.get(self.app_config.basePath + '/homevideoinfo', {
        params: {}
      }).then(function (response) {
        self.video_data = response.data;
      }).catch(function (err) {
        console.log(err);
      });

      self.$http.get(self.app_config.basePath + '/classifyinfo', {
        params: {
          classtype: 'classify1',
          searchstr: '美食'
        }
      }).then(function (response) {
        self.classify_data = response.data.list;
        let temp_breadcrumb = new Array('首页', '美食');
        window.localStorage.setItem('temp_breadcrumb', JSON.stringify(temp_breadcrumb));
      }).catch(function (err) {
        console.log(err);
      });

      self.videolist_func();

      self.$http.get(self.app_config.basePath + '/specialinfo', {
        params: {}
      }).then(function (response) {
        let temp = [],
          titlelength = 25,
          descriptionlength = 40;
        response.data.forEach(function (item, index) {
          let obj = {
            id: item.id,
            title: item.title,
            description: item.description,
            img_url: item.img_url
          };
          if (obj.title.length >= titlelength) {
            obj.title = item.title.slice(0, titlelength) + '...';
          }
          if (obj.description.length >= descriptionlength) {
            obj.description = item.description.slice(0, descriptionlength) + '...';
          }
          temp.push(obj);
        });
        self.special_data = temp;
      }).catch(function (err) {
        console.log(err);
      });
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .app-home {
    position: relative;
    width: 100%;
    min-width: 1000px
  }

  .app-home > .video-content {
    background-color: #f1f1f2;
    padding: 40px 0;
    width: 100%;
    border-bottom: 1px solid #cfcfcf
  }

  .app-home > .video-content > .inner {
    width: 1000px;
    height: 562.5px;
    margin: auto
  }

  .app-home > .middle-content {
    width: 1000px;
    padding: 40px 0;
    margin: auto;
    overflow: hidden
  }

  .app-home > .middle-content > .videolist {
    float: left;
    width: 689px
  }

  .app-home > .middle-content > .special {
    float: right;
    width: 311px
  }
</style>



