<template>
  <div class="app-classifyinfo">
    <app-breadcrumb :breadcrumb_data="breadcrumb_data"></app-breadcrumb>
    <div class="middle-content">
      <div class="left">
        <p class="title">{{classifyinfo_data.name}}配方{{classifyinfo_data.number}}个</p>
        <p class="description">{{classifyinfo_data.content}}</p>
        <div class="content">
          <p class="title">详细类别“{{classifyinfo_data.name}}”</p>
          <ul>
            <li v-for="listitem in classifyinfo_data.list" @click="goto(listitem)">{{listitem.name}}</li>
          </ul>
        </div>
        <app-videolist :videolist_data="videolist_data"></app-videolist>
      </div>
      <div class="right">
        <app-special :special_data="special_data"></app-special>
      </div>
    </div>
  </div>
</template>

<script>
  import app_breadcrumb from '@/components/App-breadcrumb'
  import app_special from '@/components/App-special'
  import app_videolist from '@/components/App-videolist'

  let classifyinfo_defautData = {
      list: [],
      name: '',
      number: '',
      content: ''
    },
    videolist_defautData = {
      title: '結果',
      pagemsg: {
        nowpage: 0,
        sumpage: 0
      },
      list: []
    },
    special_defautData = [];
  export default {
    name: 'app-classifyinfo',
    data() {
      return {
        breadcrumb_data: [],
        classifyinfo_data: classifyinfo_defautData,
        videolist_data: videolist_defautData,
        special_data: special_defautData
      }
    },
    watch: {
      '$route': function (route) {
        this.classifyinfo_func();
        this.videolist_func();
      }
    },
    components: {
      'app-breadcrumb': app_breadcrumb,
      'app-special': app_special,
      'app-videolist': app_videolist,
    },
    methods: {
      goto: function goto(listItem) {
        let self = this;
        window.localStorage.setItem('temp_breadcrumb', JSON.stringify(self.breadcrumb_data));
        self.$router.push({
          name: 'app-classifyinfo',
          query: {
            type: listItem.type,
            name: listItem.name
          }
        });
      },
      classifyinfo_func: function () {
        let self = this,
          temp_breadcrumb = JSON.parse(window.localStorage.getItem('temp_breadcrumb'));
        temp_breadcrumb.push(this.$route.query.name);
        self.breadcrumb_data = temp_breadcrumb;
        self.$http.get(self.app_config.basePath + '/classifyinfo', {
          params: {
            search_data: self.$route.query.name,
            classtype: self.$route.query.type
          }
        }).then(function (response) {
          self.classifyinfo_data = response.data;
        }).catch(function (err) {
          console.log(err);
        });
      },
      videolist_func: function () {
        let self = this;
        self.$http.get(self.app_config.basePath + '/videolistinfo', {
          params: {
            page: self.$route.query.page ? self.$route.query.page : 1,
            classify1: self.$route.query.type == 'classify1' ? self.$route.query.name : '',
            classify2: self.$route.query.type == 'classify2' ? self.$route.query.name : '',
            classify3: self.$route.query.type == 'classify3' ? self.$route.query.name : '',
            classify4: self.$route.query.type == 'classify4' ? self.$route.query.name : ''
          }
        }).then(function (response) {
          let temp = {
              title: self.$route.query.name + '视频列表',
              pagemsg: {
                nowpage: parseInt(response.data.pagemsg.nowpage),
                sumpage: parseInt(response.data.pagemsg.sumpage)
              },
              list: []
            },
            titlelength = 20,
            descriptionlength = 30,
            ingredientslength = 15;
          response.data.list.forEach(function (item, index) {
            let obj = {
              id: item.id,
              img_url: item.img_url,
              title: item.title,
              description: item.description,
              ingredients: item.ingredients
            };
            if (obj.title.length >= titlelength) {
              obj.title = item.title.slice(0, titlelength) + '...';
            }
            if (obj.description.length >= descriptionlength) {
              obj.description = item.description.slice(0, descriptionlength) + '...';
            }
            if (obj.ingredients.length >= ingredientslength) {
              obj.ingredients = item.ingredients.slice(0, ingredientslength) + '...';
            }
            temp.list.push(obj);
          });
          self.videolist_data = temp;
        }).catch(function (err) {
          console.log(err);
        });
      }
    },
    mounted() {
      let self = this;
      self.classifyinfo_func();
      self.videolist_func();
      self.$http.get(self.app_config.basePath + '/specialinfo', {
        params: {}
      }).then(function (response) {
        let temp = [],
          titlelength = 25,
          descriptionlength = 40;
        response.data.forEach(function (item, index) {
          let obj = {
            id: item.id,
            title: item.title,
            description: item.description,
            img_url: item.img_url
          };
          if (obj.title.length >= titlelength) {
            obj.title = item.title.slice(0, titlelength) + '...';
          }
          if (obj.description.length >= descriptionlength) {
            obj.description = item.description.slice(0, descriptionlength) + '...';
          }
          temp.push(obj);
        });
        self.special_data = temp;
      }).catch(function (err) {
        console.log(err);
      });
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

  .app-classifyinfo {
    width: 100%
  }

  .app-classifyinfo > .middle-content {
    width: 1000px;
    margin: auto;
    padding: 40px 0;
    overflow: hidden
  }

  .app-classifyinfo > .middle-content > .left {
    float: left;
    width: 689px;
    font-size: 16px;
    box-sizing: border-box
  }

  .app-classifyinfo > .middle-content > .left > .title {
    font-size: 26px;
    font-weight: 700;
    margin: 0 20px 0 0
  }

  .app-classifyinfo > .middle-content > .left > .description {
    margin: 16px 20px 40px 0;
    font-size: 14px;
    line-height: 1.5
  }

  .app-classifyinfo > .middle-content > .left > .content > .title {
    font-size: 20px;
    font-weight: 700;
    margin: 40px 20px 24px 0
  }

  .app-classifyinfo > .middle-content > .left > .content > ul {
    list-style: none;
    overflow: hidden;
    padding: 0;
    margin: 0
  }

  .app-classifyinfo > .middle-content > .left > .content > ul > li {
    float: left;
    width: 200px;
    height: 30px;
    line-height: 30px;
    border: 1px solid #cfcfcf;
    font-size: 14px;
    box-sizing: border-box;
    margin: 0 20px 20px 0;
    padding: 0 10px;
    color: #262626;
    cursor: pointer
  }

  .app-classifyinfo > .middle-content > .left > .content > ul > li:after {
    content: ">";
    float: right;
    color: #c6b363;
    font-size: 16px;
    font-weight: 700
  }

  .app-classifyinfo > .middle-content > .right {
    float: right;
    width: 311px
  }

</style>



