<template>
  <div class="app-classify"><p class="title">分类</p>
    <ul class="classify-list">
      <li v-for="listitem in classify_data">
        <router-link :to="{
            name:'app-classifyinfo',
            query:{type:listitem.type,name:listitem.name}
          }">
          {{listitem.name}}( {{listitem.number}})
        </router-link>
      </li>
    </ul>
  </div>
</template>

<script>
  export default {
    name: '',
    props: ['classify_data'],
    data() {
      return {}
    },
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .app-classify {
    width: 100%;
    margin-bottom: 25px
  }

  .app-classify > .title {
    font-size: 20px;
    font-weight: 700;
    margin: 0 20px 25px 0;
    padding-bottom: 8px;
    border-bottom: 1px solid #c6b363
  }

  .app-classify > .classify-list {
    margin: 0;
    padding: 0 0 10px;
    list-style: none;
    overflow: hidden
  }

  .app-classify > .classify-list > li {
    float: left;
    padding: 0 15px;
    font-size: 14px;
    border-left: 1px solid #eee;
    border-right: 1px solid #eee
  }

  .app-classify > .classify-list > li:first-child {
    padding-left: 0;
    border-left: 0
  }

  .app-classify > .classify-list > li:last-child {
    padding-right: 0;
    border-right: 0
  }

  .app-classify > .classify-list > li > a {
    text-decoration: none;
    color: #262626
  }

</style>



